/********************
 ** Slider Styles
 ********************/
.hero-slider {
    margin: 0 !important;
    width: 100vw !important;

    button {
        
        i {
            position: relative;
            width: 100%; 
            height: 100%;
            
            &::before {
                display: block;
                font-size: 22px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
        
        &:focus,
        &:hover {
            background-color: rgba(0,0,0,1) !important;
            color: white !important;
        }

        &:focus { outline: auto; }
    }
  
  /** Autoplay toggle button in the top left corner */
    button.slick-autoplay-toggle-button {
        display: block;
        left: auto;
        right: 1px;
        top: 1px;
        bottom: auto;
        padding: 1rem;
        border: 0;
        background: rgba(256,256,256,.8) !important;
        box-shadow: 1px 1px 5px rgba(0,0,0,.3);
        z-index: 10;
        opacity: 1;
        width: 45px;
        height: 45px;
        cursor: pointer;
        color: black !important;
    }
  
    /** Custom previous and next arrows in the top right corner */
    .custom-prev-button,
    .custom-next-button {
        position: absolute;
        top: calc(50% - 20px);
        z-index: 1;
        display: block;
        background: rgba(256,256,256,.8) !important;
        box-shadow: 1px 1px 5px rgba(0,0,0,.3);
        border: 0;
        padding: 0.5rem;
        margin: 0;
        cursor: pointer;
        height: 35px;
        width: 35px;
        color: black;
        border-radius: 50%;  
    } 

    .custom-next-button { right: 10px; }

    .custom-prev-button { left: 10px; }
    
    /** Default slide navigation dots */
    .slick-dots {
        bottom: 20px;
    }
    
    /** Slide content */
    .slick-slide {
        position: relative;
        height: 600px;
        display: flex;
        align-items: center;
        
        /** Float text overlay */
        .overlay {
            position: absolute;
            width: 30%;
            top: 130px; 
            padding: 20px;
            text-align: center;
            
            @media screen and (max-width: 768px) {
                width: 80%;
                left: 0 !important;
                top: 100px !important;
                text-align: left;
            }
            
            h2 {
                padding: 0;
                margin: 0;
                font-size: 28px;
            }
            
            p {
                font-size: 14px;
            }
        }
        
        .background-image {
            width: 100%;
            height: auto;
            background-size: cover;
            
            @media screen and (max-width: 768px) {
                width: auto;
                height: 600px;
                margin: 0 auto;
            }
        }
    }
}

